//стилизация бэкдропа

.backdrop {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    margin-top: 0;
    background-color:  $backdrop-color;
    overflow-y: scroll;

    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;

    @media screen and (min-width: 768px) {
        padding-left: 75px;
        padding-right: 75px;
        padding-bottom: 200px;
        padding-top: 231px;
    }

    @media screen and (min-width: 1024px) {
        padding-top: 50px;
    }
}

// базовая разметка модалки

.modal {
    background-color: $white-color;
    position: absolute;
    transform: translateX(-50%) translateY(-5%);
    top: 93px;
    left: 50%;
    overflow-y: scroll;

    width: 280px;
    height: 958px;

    padding-left: 20px;
    padding-right: 20px;
    padding-top: 48px;
    padding-bottom: 20px;

    @media screen and (min-width: 768px) {
        width: 618px;
        height: 562px;
        transform: translateX(-50%) translateY(25%);

        padding-left: 28px;
        padding-right: 28px;
        padding-bottom: 40px;
        padding-top: 48px;
    }

    @media screen and (min-width: 1024px) {
        width: 882px;
        height: 568px;
        transform: translateX(-50%) translateY(0);

        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 40px;
        padding-top: 50px;
    }
}

//стилизация крестика

.modal__cross {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: none;
    background-color: transparent;

    &:hover,
    &:focus {
        fill: $accent-btn-color;
    }
}

//обертка

.modal__wrap {
    @media screen and (min-width: 768px) {
        display: flex;
        justify-content: center;
        align-items: space-between;
    }
}

//стилизация картинки

.modal__img {
    margin-bottom: 30px;

    @media screen and (min-width: 768px) {
        width: 264px;
        height: 374px;
        margin-right: 30px;
    }

    @media screen and (min-width: 1024px) {
        width: 396px;
        height: 478px;
    }
}

//заголовок

.modal__title {
    margin-bottom: 20px;

    @media screen and (min-width: 768px) {
        margin-top: 0;
    }
}

//стилизация списков

.modal__list {
    display: flex;
    margin-bottom: 20px;

    align-items: center;
    font-size: 12px;
    line-height: 16px;
}

.modal__params {
    margin-right: 40px;
}

.modal__item {
    color: $secondary-text-color;

    &:not(:first-child) {
        margin-top: 10px;
    }
}

.modal__meaning {
    &:not(:first-child) {
        margin-top: 10px;
    }
}

.modal__meaning--orange {
    padding-left: 7px;
    padding-right: 7px;

    border-radius: 20%;
    border: 1px solid $accent-btn-color;
    background-color: $accent-btn-color;
    color: $white-color;
}

.modal__meaning--grey {
    padding-left: 7px;
    padding-right: 7px;

    border-radius: 20%;
    border: 1px solid $beige-color;
    background-color: $beige-color;
}

//описание

.modal__desc {
    margin-bottom: 20px;
    margin-top: 10px;

    font-size: 12px;
    line-height: 20px;
    
}

//кнопки

.modal__wrap--btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;

    @media screen and (min-width: 1024px) {
        justify-content: flex-start;
    }
}

//скрытие модалки

.modal--close {
    display: none;
}